import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css'
import 'font-awesome/css/font-awesome.min.css'
import './SkillButton.css'
import constant from './Const'
import {IntentButtonGroup} from './Buttons'


export class Button extends React.Component {

    constructor(props) {
        super(props);
        this.handleChange = this.handleChange.bind(this);
        this.onClick = this.onClick.bind(this);
        this.btn_name = props.children;
        this.show = false;
        this.intentValue = this.initializeIntentValue(props.data);
        this.onClickCb = this.props.onClick;
        if(!this.onClickCb) this.onClickCb = this.onClick;
    }

    initializeIntentValue(data) {
        let intentValue = data;
        if(!intentValue) {
            intentValue = [];
        }
        return intentValue;
    }

    handleChange(value) {
        console.log('intent value change', value);
        this.intentValue = value;
    }

    onClick() {
        this.show = !this.show;
        this.forceUpdate()
    }

    render() {
        let className = this.props.className;
        if(!className) className = '';
        let append = null;
        if(this.show) append = <IntentButtonGroup onChange={this.handleChange} value={this.intentValue} defaultButtonName="unknown intent"/>;
        return(
            <div className="skill_btn_container">
                <div className={constant.buttonClass().skill_btn+' '+className} onClick={this.onClickCb}>
                    <p className="skill_btn_name">{this.btn_name}</p>
                </div>
                {append}
            </div>);
    }

}

export class AddButton extends Button {

    constructor(props){
        super(props);
        this.intentGroup = undefined;
        this.btn_name = [<i key='1' className="fa fa-plus-circle add_skill_btn_icon" aria-hidden="true"/>, 'Add New Skill'];
    }

}


