import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css'
import './RepeatsGroup.css'
import {InputFieldGroup} from './Fields'
import {AddElementButton, RemoveElementButton} from '../buttons/Buttons'
import {ControlLabel, Button} from 'react-bootstrap'
import {IdGenerator} from '../../../util/Utils'

export default class RepeatsGroup extends React.Component {

    constructor() {
        super();
        this.value = [];
        this.addNewRepeat = this.addNewRepeat.bind(this);
        this.update = this.update.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.removeElement = this.removeElement.bind(this);
        this.repeats = [];
        this.state = {
            labelName: "Repeats"
        };
        this.addBtn = <AddElementButton onClick={this.addNewRepeat} />;
    }

    addNewRepeat() {
        let i = this.repeats.length;
        this.repeats.push(<RepeatContainer key={IdGenerator.getNewId()} repeatGroup={this.repeats} attr={{index: i}} removeSelf={this.removeElement}/>);
        this.value.push({
            slots: [],
            grams: []
        });
        this.update();
    }

    update() {
        if (this.props.onChange && typeof this.props.onChange === "function") {
            this.props.onChange(this.value);
        }
        else {
            console.error('repeats group onChange is not a function');
        }
        this.forceUpdate();
    }

    removeElement(index) {
        this.repeats.splice(index, 1);
        this.value.splice(index, 1);
        this.update();
    }

    handleChange(value, index) {
        if (this.props.onChange && typeof this.props.onChange === "function") {
            this.value[index] = value;
            this.props.onChange(Object.assign([], this.value));
        }
        else {
            console.error('repeats group onChange is not a function');
        }
    }

    render() {
        return (
            <div className="repeats-group">
                <ControlLabel>{this.state.labelName}</ControlLabel>
                <div className="repeats-group-container">
                    {this.repeats.map((value, i) => {
                        value.props.attr.index = i;
                        value.props.attr.onChange = this.handleChange;
                        return value;
                    })}
                    {this.addBtn}
                </div>
            </div>
        )
    }

}

class RepeatContainer extends React.Component {

    constructor(props) {
        super(props);
        this.removeSelf = this.removeSelf.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.repeatGroup = props.repeatGroup;
        this.remove_btn = <RemoveElementButton className='repeat-remove-btn' onClick={this.removeSelf}/>
    }

    removeSelf() {
        this.props.removeSelf(this.props.attr.index);
    }

    handleChange(value) {
        if(!this.props.attr) {
            console.error('RepeatContainer has not attr');
        }
        else if(!this.props.attr.onChange || !(typeof this.props.attr.onChange === "function")) {
            console.error('RepeatContainer\'s onChange is not a function');
        }
        else {
            this.props.attr.onChange(value, this.props.attr.index);
        }
    }

    render() {
        return(
            <div className="repeat-container">
                <Repeat onChange={this.handleChange} />
                {this.remove_btn}
            </div>
        )
    }

}

class Repeat extends React.Component {

    constructor(props) {
        super(props);
        this.handleSlotsChange = this.handleSlotsChange.bind(this);
        this.handleGramsChange = this.handleGramsChange.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.value = {
            slots: [],
            grams: []
        };
        this.slots = <InputFieldGroup onChange={this.handleSlotsChange} labelName="slots"/>;
        this.grams = <InputFieldGroup onChange={this.handleGramsChange} labelName="grams"/>;
        this.state = {
            labelName: 'Repeat'
        };
    }

    handleSlotsChange(value) {
        this.value.slots = value;
        this.handleChange();
    }

    handleGramsChange(value) {
        this.value.grams = value;
        this.handleChange();
    }

    handleChange() {
        this.props.onChange(Object.assign({}, this.value));
    }

    render() {
        return(
            <div className="repeat">
                <ControlLabel>{this.state.labelName}</ControlLabel>
                <div className="repeat-entity">
                    {this.slots}
                    {this.grams}
                </div>
            </div>
        )
    }

}



