import React, { Component } from 'react';
import rsvp_logo from '../public/img/rsvp.png';
import './App.css';
import CurrentComponent from './CurrentComponent'

class App extends Component {
  render() {
    return (
      <div className="App">
        <div className="App-header">
          <img src={rsvp_logo} className="App-logo" alt="logo" />
            <h3>RSVP Grammar Toolkit</h3>
        </div>
          <CurrentComponent/>
      </div>
    );
  }
}

export default App;



