import React from 'react'
import 'bootstrap/dist/css/bootstrap.css'
import './ServiceComponent.css'
import Table from 'react-bootstrap/lib/Table'
import AddNewServiceButton from './components/AddNewServiceButton'


export default class ServiceComponent extends React.Component {

    constructor(props) {
        super(props);
        this.header = <ServiceHeader update={props.update}/>
        this.table = <ServiceTable />
    }

    render() {
        return(
            <div id="service_component">
                {this.header}
                {this.table}
            </div>
        )
    }
}

class ServiceHeader extends React.Component {

    constructor() {
        super();
    }

    render() {
        return(
            <div>
                <div className="row">
                    <h4 className="col-lg-10 col-md-10 col-sm-10" id="service_header">Building RSVP Grammar Skills with Gram Toolkit</h4>
                    <div id="add_new_service_btn" className="col-lg-2 col-md-2 col-sm-2"><AddNewServiceButton update={this.props.update}/></div>
                </div>
                <hr/>
                <p id="service_header_intro">To learn more about building customized service, see <a href="#">Getting Started with the Gram Service Kit</a></p>
            </div>
        )
    }

}

class ServiceTable extends React.Component {

    constructor() {
        super();
        this.tableElements = {
            tableHead: ['Name', 'Language', 'Type', 'Modified', 'Status', 'Actions'],
            tableBody: [
                this.tableBodyRow('Vertical', 'Chinese', 'Default', '2/5/17', 'development', <a href="#">edit</a>),
                this.tableBodyRow('RChat', 'Chinese', 'Default', '4/7/17', 'development', <a href="#">edit</a>)
                ]
        };



        this.tableHead = <thead>
                            <tr>
                                {this.tableElements.tableHead.map((elem, i) => <th key={i}>{elem}</th>)}
                            </tr>
                        </thead>;
        this.tableBody = <tbody>
                            {this.tableElements.tableBody.map((elem, i) =>
                                <tr key={i}>
                                    {
                                        Object.keys(elem).map((key, i) => <td key={i}>{elem[key]}</td>)
                                    }
                                </tr>)}
                        </tbody>
    }

    tableBodyRow(name, language, type, modified, status, actions) {
        return {
            name: name,
            language: language,
            type: type,
            modified: modified,
            status: status,
            actions: actions
        };
    }

    render() {
        return(
            <div id="service_table_container">
                <Table striped bordered condensed hover>
                    {this.tableHead}
                    {this.tableBody}
                </Table>
            </div>
        )
    }

}


