import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css'
import './TermComponent.css'
import TermComponentSide from './components/TermComponentSide'
import TermComponentBody from './components/TermComponentBody'

export default class TermComponent extends React.Component {

    constructor(props) {
        super(props);
        this.update = this.update.bind(this);
        this.updateCurrentComponent = props.update;
    }

    update() {
        this.forceUpdate();
    }

    render() {
        return(
            <div className="row">
                <div className="col-lg-2 col-md-2 col-sm-2 term-component">
                    <TermComponentSide/>
                </div>
                <div className="col-lg-10 col-md-10 col-sm-10 term-component">
                    <TermComponentBody update={this.updateCurrentComponent}/>
                </div>
            </div>
        )
    }

}


