import React from 'react'
import './IntentButtonGroup.css'
import {InputField, ModalField} from '../fields/Fields'
import {Intent} from './Buttons'
import 'bootstrap/dist/css/bootstrap.min.css'
import {Button} from 'react-bootstrap'
import {IdGenerator, IntentIDGenerator} from '../../../util/Utils'

export default class IntentButtonGroup extends React.Component {

    constructor(props) {
        super(props);
        this.initializeBtnGroup = this.initializeBtnGroup.bind(this);
        this.showModal = this.showModal.bind(this);
        this.addNewButton = this.addNewButton.bind(this);
        this.getNewButtonName = this.getNewButtonName.bind(this);
        this.getValue = this.getValue.bind(this);
        this.btnGroup =[];
        this.initializeBtnGroup(props.value);
        this.className = props.className;
        this.modalTile = 'New Intent';
        this.addBtn = <Intent.AddButton onClick={this.showModal} />;
        this.state = {
            show: false
        };
        this.newButtonName = props.defaultButtonName;
        this.yesBtn = <Button onClick={this.addNewButton}>Confirm</Button>;
        this.inputField = <InputField onPressEnter={this.addNewButton} onChange={this.getNewButtonName}/>;
    }

    initializeBtnGroup(value) {
        if(value) {
            value.map((v,k) => {
                this.btnGroup.push(<Intent.Button key={IdGenerator.getNewId()}>{v['intent_name']}</Intent.Button>);
            });
        }
    }

    showModal() {
        this.setState({show: true});
    }

    getNewButtonName(name) {
        this.newButtonName = name;
    }

    getValue() {
        let value = [];
        this.btnGroup.map((intent) => {
            let obj = {};
            obj['intent_name'] = intent.props.children;
            value.push(obj);
        });
        return value;
    }

    addNewButton() {
        let key = IdGenerator.getNewId();
        let intentId = IntentIDGenerator.getNewId();
        console.log(intentId);
        this.btnGroup.push(<Intent.Button key={key} id={intentId}>{this.newButtonName}</Intent.Button>);
        if(this.props.onChange && (typeof this.props.onChange === 'function')) {
            let value = this.getValue();
            this.props.onChange(value);
        }
        this.newButtonName = this.props.defaultButtonName;
        this.setState({show: false});
    }

    removeButton(id) {
        delete this.value[id];
    }

    render() {
        return(
            <div className='skill_btn_group'>
                {this.btnGroup.map((value, i) => value)}
                {this.addBtn}
                <ModalField title={this.modalTile} show={this.state.show} body={this.inputField} btn={this.yesBtn}/>
            </div>
        )
    }

}


