import React from 'react'
import './IntentButton.css'
import constant from './Const'
import currentIntent from '../CurrentIntent'

class BaseButton extends React.Component {

    constructor(props) {
        super(props);
        this.onClick = props.onClick;
        this.btn_name = props ? props.children : undefined;
    }

    render() {
        let className = this.props.className;
        if(!className) className = '';
        return(<div className={constant.buttonClass().intent_btn+' '+className} onClick={this.onClick}>
            <p className="intent_btn_name">{this.btn_name}</p>
        </div>);
    }
}


export class Button extends BaseButton {

    constructor(props) {
        super(props);
        this.onClick = this.handleClick.bind(this);
        this.setIntentValue = this.setIntentValue.bind(this);
        this.intent = props.value ? props.value : {};
        currentIntent.setAssociatedValueSetter(this.setIntentValue);
    }

    setIntentValue(value) {
        this.intent = Object.assign({}, value);
    }

    handleClick() {
        currentIntent.setAssociatedValueSetter(this.setIntentValue);
        currentIntent.setCurrentIntentValue(Object.assign({}, this.intent));
        currentIntent.showIntent();
        currentIntent.updateTermBody();
    }

}

export class AddButton extends BaseButton {

    constructor(props){
        super(props);
        this.btn_name = [<i key='1' className="fa fa-plus-circle add_skill_btn_icon" aria-hidden="true"/>, 'Add New Intent'];
    }

}


