import React from 'react'
import 'bootstrap/dist/css/bootstrap.css'
import './AddNewServiceButton.css'
import {Button} from 'react-bootstrap'
import config from '../../Config'

export default class AddNewServiceButton extends React.Component {

    constructor(props) {
        super(props);
        this.btn_name = 'Add a New Service';
    }

    render() {
        return(
            <Button className="btn btn-primary" onClick={
                () => {
                    this.props.update(config.registeredComponents().termComponent)
                }
            }>{this.btn_name}</Button>
        )
    }


}


