import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css'
import 'font-awesome/css/font-awesome.min.css'
import './TermButtons.css'
import config from '../../../Config'

export default class BackButton extends React.Component {

    constructor() {
        super();
        this.handleClick = this.handleClick.bind(this);
    }

    handleClick() {
        if(!this.props.update || (typeof this.props.update !== 'function')) return;
        this.props.update(config.registeredComponents().serviceComponent);
    }

    render() {
        return(
            <div id="back_btn" className="term_btn" >
                <i onClick={this.handleClick} data-tog aria-hidden="true" id="back_btn_icon" className="fa fa-caret-square-o-right term_btn_icon"></i>
            </div>
        )
    }

}


