import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css'
import './IntentComponent.css'
import 'whatwg-fetch'
import config from '../../Config'
import constant from '../data/Constant'
import {InputField, InputFieldGroup, RepeatsGroup, BooleanSelection, SlotsGroup} from './fields/Fields'

export default class IntentComponent extends React.Component {

    constructor(props) {
        super(props);
        this.checkValidationAndInit = this.checkValidationAndInit.bind(this);
        this.init = this.init.bind(this);
        this.reRender = this.reRender.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.handleIntentNameChange = this.handleIntentNameChange.bind(this);
        this.handleEntryGramsChange = this.handleEntryGramsChange.bind(this);
        this.handleRepeatsGroupChange = this.handleRepeatsGroupChange.bind(this);
        this.handleUtterancesChange = this.handleUtterancesChange.bind(this);
        this.handleConfirmsChange = this.handleConfirmsChange.bind(this);
        this.handleSlotsChange = this.handleSlotsChange.bind(this);
        this.handleRequireConfirmChange = this.handleRequireConfirmChange.bind(this);
        this.handleConfirmGramChange = this.handleConfirmGramChange.bind(this);
        this.handleConfirmKeyChange = this.handleConfirmKeyChange.bind(this);
        this.handleConfirmValueSuccessChange = this.handleConfirmValueSuccessChange.bind(this);
        this.handleConfirmValueFailChange = this.handleConfirmValueFailChange.bind(this);
        this.msg = 'default';
        this.ref = null;
        this.value = {};
    }

    init(value) {
        this.checkValidationAndInit(value, constant.intent().intent_name, '');
        this.checkValidationAndInit(value, constant.intent().entry_grams, []);
        this.checkValidationAndInit(value, constant.intent().slots, []);
        this.checkValidationAndInit(value, constant.intent().utterances, []);
        this.checkValidationAndInit(value, constant.intent().repeats, []);
        this.checkValidationAndInit(value, constant.intent().require_confirm, false);
        this.checkValidationAndInit(value, constant.intent().confirms, []);
        this.checkValidationAndInit(value, constant.intent().confirm_gram, '');
        this.checkValidationAndInit(value, constant.intent().confirm_key, '');
        this.checkValidationAndInit(value, constant.intent().confirm_value_success, '');
        this.checkValidationAndInit(value, constant.intent().confirm_value_fail, '');
    }

    checkValidationAndInit(value, fieldName, initValue) {
        if(!value || !value[fieldName]) {
            this.value[fieldName] = initValue;
        }
        else {
            this.value[fieldName] = value[fieldName];
        }
    }

    handleChange() {
        if(this.props.onChange && (typeof this.props.onChange) === 'function'){
            this.props.onChange(Object.assign({}, this.value));
        }
    }

    handleIntentNameChange(value) {
        this.value[constant.intent().intent_name] = value;
        console.log('intent name change ', value, this.value)
        this.handleChange();
    }

    handleEntryGramsChange(value) {
        this.value[constant.intent().entry_grams] = value;
        this.handleChange();
    }

    handleRepeatsGroupChange(value) {
        this.value[constant.intent().repeats] = value;
        this.handleChange();
    }

    handleUtterancesChange(value) {
        this.value[constant.intent().utterances] = value;
        this.handleChange();
    }

    handleConfirmsChange(value) {
        this.value[constant.intent().confirms] = value;
        this.handleChange();
    }

    handleSlotsChange(value) {
        this.value[constant.intent().slots] = value;
        this.handleChange();
    }

    handleRequireConfirmChange(value) {
        this.value[constant.intent().require_confirm] = value;
        this.handleChange();
    }

    handleConfirmGramChange(value) {
        this.value[constant.intent().confirm_gram] = value;
        this.handleChange();
    }

    handleConfirmKeyChange(value) {
        this.value[constant.intent().confirm_key] = value;
        this.handleChange();
    }

    handleConfirmValueSuccessChange(value) {
        this.value[constant.intent().confirm_value_success] = value;
        this.handleChange();
    }

    handleConfirmValueFailChange(value) {
        this.value[constant.intent().confirm_value_fail] = value;
        this.handleChange();
    }

    render() {
        this.init(this.props.value);
        console.log('render', this.props.value," intent value name ", this.value[constant.intent().intent_name]);
        return(
            <div id="intent-component">
                <p>{this.msg}</p>
                <div id="btn_group_container">
                    <InputField  attr={{value: this.value[constant.intent().intent_name]}} labelName="Intent Name" placeholder="Intent Name" disabled={false} onChange={this.handleIntentNameChange} />
                    <InputFieldGroup value={this.value[constant.intent().entry_grams]} labelName="Entry Grams" onChange={this.handleEntryGramsChange}/>
                    <InputFieldGroup value={this.value[constant.intent().utterances]} labelName="Utterances" onChange={this.handleUtterancesChange}/>
                    <InputFieldGroup value={this.value[constant.intent().confirms]} labelName="Confirms" onChange={this.handleConfirmsChange}/>
                    <RepeatsGroup labelName="Repeats" onChange={this.handleRepeatsGroupChange} />
                    <SlotsGroup labelName='Slots' onChange={this.handleSlotsChange}/>
                    <BooleanSelection labelName='Require Confirm' onChange={this.handleRequireConfirmChange}/>
                    <InputField labelName="Confirm Gram" placeholder="confirm gram" disabled={false} onChange={this.handleConfirmGramChange}/>
                    <InputField labelName="Confirm Key" placeholder="confirm key" disabled={false} onChange={this.handleConfirmKeyChange}/>
                    <InputField labelName="Confirm Value Success" placeholder="confirm value success" disabled={false} onChange={this.handleConfirmValueSuccessChange}/>
                    <InputField labelName="Confirm Value Fail" placeholder="confirm value fail" disabled={false} onChange={this.handleConfirmValueFailChange}/>
                </div>
            </div>
        )
    }

    reRender() {
        console.log('body re-render');
        this.forceUpdate();
    }

    componentDidMount() {
        fetch(config.path().root+'/test/data')
            .then((response) => response.json())
            .then((response)=>{
                console.log(response);
                this.msg = response['key'];
                this.reRender();
            })
            .catch((ex) => {
                console.log('exception:', ex);
            });
    }
}




