import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css'
import './TermComponentBody.css'
import {SaveModelButton, BuildModelButton, DeleteButton, BackButton} from './buttons/Buttons'
import 'whatwg-fetch'
import IntentComponent from './IntentComponent'
import currentIntent from './CurrentIntent'

export default class TermComponentBody extends React.Component {

    constructor(props) {
        super(props);
    }

    render() {
        return(
            <div id="term_component_body">
                <TermComponentBodyHead update={this.props.update}/>
                <TermComponentBodyEntity/>
            </div>
        )
    }

}

class TermComponentBodyHead extends React.Component {

    constructor(props) {
        super(props);
    }

    buttonContainer(btn) {
        return(
            <div className="col-lg-1 col-md-1 col-sm-1 term_component_body_btn_container">
                {btn}
            </div>
        )
    }

    render() {
        return(
            <div id="term_component_body_head" className="row">
                {this.buttonContainer(<SaveModelButton/>)}
                {this.buttonContainer(<BuildModelButton/>)}
                {this.buttonContainer(<DeleteButton/>)}
                {this.buttonContainer(<BackButton update={this.props.update}/>)}
            </div>
        )
    }

}

class TermComponentBodyEntity extends React.Component {

    constructor(props) {
        super(props);
        this.update = this.update.bind(this);
        this.handleOnChange = this.handleOnChange.bind(this);
        this.value = '';
        this.show = false;
        currentIntent.setUpdateCb(this.update);
    }

    update() {
        this.forceUpdate();
    }

    handleOnChange(value) {
        currentIntent.saveIntentValue(value);
    }

    render() {
        let entity = undefined;
        if(currentIntent.isShow()) {
            console.log('re render term body',currentIntent.getCurrentIntentValue());
            entity = <IntentComponent value={currentIntent.getCurrentIntentValue()} onChange={this.handleOnChange}/>;
        }
        return(
            <div id="term_component_body_entity">
                {entity}
            </div>
        )
    }

}



