import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css'
import './SlotsGroup.css'
import {Button, ControlLabel} from 'react-bootstrap'
import {InputField, BooleanSelection, InputFieldGroup, ModalField} from './Fields'
import {AddElementButton, RemoveElementButton} from '../buttons/Buttons'
import {IdGenerator} from '../../../util/Utils'


export default class SlotsGroup extends React.Component {

    constructor() {
        super();
        this.addNewSlot = this.addNewSlot.bind(this);
        this.update = this.update.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.removeElement = this.removeElement.bind(this);
        this.slotsGroup = [];
        this.state = {
            labelName: "Slots"
        };
        this.addSlotBtn = <AddElementButton onClick={this.addNewSlot}/>
    }

    addNewSlot() {
        this.slotsGroup.push(<Slot key={IdGenerator.getNewId()} attr={{}} removeSelf={this.removeElement}/>);
        this.update();
    }

    removeElement(index) {
        this.slotsGroup.splice(index, 1);
        this.update();
    }

    update() {
        this.forceUpdate();
    }

    handleChange(value, index) {
        this.props.onChange()
    }

    render() {
        return(
            <div className="slots">
                <ControlLabel>{this.state.labelName}</ControlLabel>
                <div className="slots-container">
                    {this.slotsGroup.map((value, i) => {
                        value.props.attr.index = i;
                        value.props.attr.onChange = this.handleChange;
                        return value;
                    })}
                    {this.addSlotBtn}
                </div>
            </div>
        )
    }

}

class Slot extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            show: false,
            title: 'Slot',
            modalValue: {}
        };
        this.showModal = this.showModal.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.removeSelf = this.removeSelf.bind(this);
        this.entity = <SlotEntity onChange={this.handleChange} attr={{value: undefined}}/>;
        this.removeBtn = <RemoveElementButton className="slot-remove-btn" onClick={this.removeSelf}/>;
    }

    showModal() {
        console.log('modal value:', this.state.modalValue );
        this.setState({show: true});
    }

    handleChange(value) {
        //TODO
        this.state.modalValue = value;
        console.log('slot value',value);
        this.props.attr.onChange(value);
        this.setState({modalValue: value});
    }

    removeSelf() {
        this.props.removeSelf(this.props.attr.index);
    }

    render() {
        console.log('rerender slot');
        let show = this.state.show;
        return(
            <div className="slot-elem">
                <Button className="slot-btn" onClick={this.showModal}>{this.state.title}</Button>
                {this.removeBtn}
                <ModalField show={show} body={this.entity} value={this.state.modalValue} title={this.state.title}/>
            </div>
        )
    }

}

class SlotEntity extends React.Component {

    constructor(props) {
        super(props);
        this.handleChange = this.handleChange.bind(this);
        this.handleSlotNameChange = this.handleSlotNameChange.bind(this);
        this.handleSlotTypeChange = this.handleSlotTypeChange.bind(this);
        this.handleIsRequiredChange = this.handleIsRequiredChange.bind(this);
        this.handlePromptsChange = this.handlePromptsChange.bind(this);
        console.log('slot construct ', props.attr.value);
        this.value = {
            slot_name: '',
            slot_type: '',
            is_required: '',
            prompts:[]
        };
    }

    handleChange() {
        this.props.onChange(this.value);
        this.forceUpdate();
    }

    handleSlotNameChange(value) {
        this.value.slot_name = value;
        this.handleChange();
    }

    handleSlotTypeChange(value) {
        this.value.slot_type = value;
        this.handleChange();
    }

    handleIsRequiredChange(value) {
        this.value.is_required = value;
        this.handleChange();
    }

    handlePromptsChange(value) {
        this.value.prompts = value;
        this.handleChange();
    }

    render() {
        if(this.props.attr.value) {
            this.value = this.props.attr.value;
        }
        return(
            <div className="intent-slot">
                <InputField attr={{value: this.value.slot_name}} onChange={this.handleSlotNameChange} labelName="Slot Name" />
                <InputField attr={{value: this.value.slot_type}} onChange={this.handleSlotTypeChange} labelName="Slot Type"/>
                <BooleanSelection value={this.value.is_required} onChange={this.handleIsRequiredChange} labelName="Is Required"/>
                <InputFieldGroup value={this.value.prompts} onChange={this.handlePromptsChange} labelName="Prompts"/>
            </div>
        )
    }
}



