import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css'
import './TermComponentSide.css'
import 'font-awesome/css/font-awesome.min.css'
import {SkillButtonGroup} from './buttons/Buttons'

export default class TermComponentSide extends React.Component {

    constructor(props) {
        super(props);
        this.data = props.data;
        if(!this.data) {
            this.data = {};
        }
    }

    render() {
        return(
            <div id="term_component_side">
                <TermComponentSideHead/>
                <TermComponentSideBody data={this.data}/>
            </div>
        );
    }

}

class TermComponentSideHead extends React.Component {

    constructor() {
        super();
    }

    render() {
        return(
            <div id="term_component_side_head">

            </div>
        )
    }

}

class TermComponentSideBody extends React.Component {

    constructor(props) {
        super(props);
    }

    render() {
        return(
            <div id="term_component_side_body">
                <SkillButtonGroup data={this.props.data}/>
            </div>
        )
    }
}



