

class CurrentIntent {

    constructor() {
        this.setCurrentIntentValue = this.setCurrentIntentValue.bind(this);
        this.getCurrentIntentValue = this.getCurrentIntentValue.bind(this);
        this.setUpdateCb = this.setUpdateCb.bind(this);
        this.updateTermBody = this.updateTermBody.bind(this);
        this.setAssociatedValueSetter = this.setAssociatedValueSetter.bind(this);
        this.saveIntentValue = this.saveIntentValue.bind(this);
        this.showIntent = this.showIntent.bind(this);
        this.hideIntent = this.hideIntent.bind(this);
        this.isShow = this.isShow.bind(this);
        this.update = undefined;
        this.currentIntentValue = {};
        this.associatedValueSetter = undefined;
        this.show = false;
    }

    setAssociatedValueSetter(setter) {
        this.associatedValueSetter = setter;
    }

    saveIntentValue(value) {
        if(this.associatedValueSetter && typeof this.associatedValueSetter === 'function') {
            this.associatedValueSetter(value);
        }
    }

    setCurrentIntentValue(currentIntentValue) {
        console.log('set',currentIntentValue,' to current intent')
        this.currentIntentValue = currentIntentValue;
    }

    getCurrentIntentValue() {
        return this.currentIntentValue;
    }

    setUpdateCb(update) {
        this.update = update;
    }

    updateTermBody() {
        if(this.update && typeof this.update === 'function') {
            this.update();
        }
    }

    isShow() {
        return this.show;
    }

    showIntent() {
        this.show = true;
    }

    hideIntent() {
        this.show = false;
    }

}

const currentIntent = new CurrentIntent();
Object.freeze(CurrentIntent);
export default currentIntent;




