import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css'
import './SkillButtonGroup.css'
import {Button} from 'react-bootstrap'
import {Skill} from './Buttons'
import {ModalField} from '../../../toolkit/fields/Fields'
import {InputField} from '../fields/Fields'
import {IdGenerator} from '../../../util/Utils'

export default class SkillButtonGroup extends React.Component {

    constructor(props) {
        super(props);
        this.showModal = this.showModal.bind(this);
        this.addNewButton = this.addNewButton.bind(this);
        this.getNewButtonName = this.getNewButtonName.bind(this);
        this.btnGroup = this.createButtonGroup(props.data);
        this.modalTile = 'New Skill';
        this.addBtn = <Skill.AddButton onClick={this.showModal} />;
        this.state = {
            show: false
        };
        this.newButtonName = 'UNKNOWN';
        this.yesBtn = <Button onClick={this.addNewButton}>Confirm</Button>;
        this.inputField = <InputField attr={{value: this.newButtonName}} onPressEnter={this.addNewButton} onChange={this.getNewButtonName}/>;
    }

    createButtonGroup(data) {
        let btnGroup = [];
        if(data) {
            Object.keys(data).map((key) =>{
                btnGroup.push(<Skill.Button key={IdGenerator.getNewId()}>{key}</Skill.Button>)
            });
        }
        return btnGroup;
    }

    showModal() {
        this.setState({show: true});
    }

    getNewButtonName(name) {
        this.newButtonName = name;
    }

    addNewButton() {
        console.log('add btn',this.newButtonName);
        this.btnGroup.push(<Skill.Button key={IdGenerator.getNewId()}>{this.newButtonName}</Skill.Button>);
        this.setState({show: false});
        this.inputField.props.attr.value = this.newButtonName;
        this.forceUpdate();
    }

    render() {
        return(
            <div className='skill_btn_group'>
                {this.btnGroup.map((value, i) => value)}
                {this.addBtn}
                <ModalField title={this.modalTile} show={this.state.show} body={this.inputField} btn={this.yesBtn}/>
            </div>
        )
    }

}


