import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css'
import 'font-awesome/css/font-awesome.min.css'
import './TermButtons.css'

export default class BuildModelButton extends React.Component {

    constructor() {
        super();
    }

    render() {
        return(
            <div id="build_model_btn" className="term_btn">
                <i data-tog id='build_model_btn_icon' className="fa fa-wrench fa-3x term_btn_icon"/>
            </div>
        )
    }

}


