
class Constant {

    skill() {
        return {
            skill_name : 'skill_name',
            intents : 'intents'
        }
    }

    intent() {
        return {
            intent_name : 'intent_name',
            entry_grams : 'entry_grams',
            slots : 'slots',
            utterances : 'utterances',
            repeats : 'repeats',
            require_confirm : 'require_confirm',
            confirms : 'confirms',
            confirm_gram : 'confirm_gram',
            confirm_key : 'confirm_key',
            confirm_value_success : 'confirm_value_success',
            confirm_value_fail : 'confirm_value_fail'
        }
    }

    intentSlot() {
        return {
            slot_name : 'slot_name',
            slot_type : 'slot_type',
            is_required : 'is_required',
            prompts : 'prompts'
        }
    }

    intentRepeat() {
        return {
            slots : 'slots',
            grams : 'grams'
        }
    }

}

const constant = new Constant();
Object.freeze(Constant);
export default constant;




