import React from 'react'
import './InputField.css'
import 'bootstrap/dist/css/bootstrap.min.css'
import {FormGroup, Form, FormControl, ControlLabel, Button} from 'react-bootstrap'


export default class InputField extends React.Component {

    constructor(props) {
        super(props);
        this.handleKeyPress = this.handleKeyPress.bind(this);
        this.handleEnterKey = this.handleEnterKey.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.labelName = props.labelName;
        this.placeholder = props.placeholder;
        this.type = props.type;
        this.label = props.label;
        this.disabled = props.disabled;
        this.className = props.className;
        console.log(props.attr);
        this.text = {value: props.attr ? props.attr.value : ''};
        if(!Object.values(inputFieldClass.allClasses()).includes(this.className)) {
            this.className = inputFieldClass.allClasses().large;
        }
        this.selfChanging = false;
    }

    handleEnterKey() {
        if(this.props.onPressEnter && typeof this.props.onPressEnter === 'function') {
            this.props.onPressEnter();
        }
    }

    handleKeyPress(key) {
        if(key.charCode === 13) {
            this.handleEnterKey();
        }
    }

    handleChange(event) {
        let value = event.target.value;
        this.text.value = value;
        this.props.onChange(value);
        this.selfChanging = true;
        this.forceUpdate();
    }

    render() {
        let className = "input-field-" + this.className;
        let controlLabel = null;
        if(this.labelName !== null && this.labelName !== '') controlLabel = <ControlLabel>{this.labelName}</ControlLabel>;
        if(this.props.attr && !this.selfChanging) {
            let value = this.props.attr.value;
            this.text = {value: value};
        }
        this.selfChanging = false;
        return(
            <FormGroup className={className}>
                {controlLabel}
                <FormControl type={this.type} placeholder={this.placeholder} label={this.label} disabled={this.disabled}
                onKeyPress={this.handleKeyPress} onChange={this.handleChange} value={this.text.value}/>
            </FormGroup>
        )
    }

}

class InputFieldClass {

    allClasses() {
        return {
            large : 'large',
            medium : 'medium',
            small : 'small'
        }
    }
}

var inputFieldClass = new InputFieldClass();



