import React from 'react'
import './InputFieldGroup.css'
import 'bootstrap/dist/css/bootstrap.min.css'
import 'font-awesome/css/font-awesome.min.css'
import {Button, ControlLabel} from 'react-bootstrap'
import InputField from './InputField'
import {AddElementButton, RemoveElementButton} from '../buttons/Buttons'
import {IdGenerator} from '../../../util/Utils'


export default class InputFieldGroup extends React.Component {

    constructor(props) {
        super(props);
        this.addNewInputField = this.addNewInputField.bind(this);
        this.update = this.update.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.calculateValue = this.calculateValue.bind(this);
        this.removeElement = this.removeElement.bind(this);
        this.initializeInputGroup = this.initializeInputGroup.bind(this);
        this.inputGroup = [];
        this.value = [];
        this.labelName = null;
        if(props.labelName) {
            this.labelName = <ControlLabel className="input-field-group-label">{props.labelName}</ControlLabel>
        }
        this.addBtn = <AddElementButton onClick={this.addNewInputField}/>;
        this.selfChanging = false;
    }

    initializeInputGroup() {
        this.inputGroup = [];
        if(this.props.value) {
            this.value = this.props.value;
            let initialValue = this.props.value;
            initialValue.map((value, i) => {
                this.inputGroup.push(<InputFieldPair key={IdGenerator.getNewId()} inputGroup={this.inputGroup}
                                                     removeSelf={this.removeElement}
                                                     attr={{
                                                         index: i,
                                                         value: value,
                                                         onChange: this.handleChange
                                                     }}/>);
            });
            console.log('****************',initialValue);
        }
    }

    calculateValue() {
        this.inputGroup.map((value, i) => {
            this.value[i] = value;
        })
    }

    removeElement(index) {
        this.inputGroup.splice(index, 1);
        this.value.splice(index, 1);
        this.update();
    }

    update() {
        if(this.props.onChange && (typeof this.props.onChange === "function")) {
            this.props.onChange(this.value);
        }
        this.forceUpdate();
    }

    addNewInputField() {
        this.inputGroup.push(<InputFieldPair key={IdGenerator.getNewId()} inputGroup={this.inputGroup} removeSelf={this.removeElement} attr={{index : null, onChange: this.handleChange}}/>);
        this.value.push('');
        this.update();
    }

    handleChange(value, index) {
        if(this.props.onChange && (typeof this.props.onChange === "function")) {
            this.value[index] = value;
            this.props.onChange(Object.assign([], this.value));
        }
        this.selfChanging = true;
        this.forceUpdate();
    }

    render() {
        if(!this.selfChanging) {
            this.initializeInputGroup();
        }
        this.selfChanging = false;
        return(
            <div className="input-field-group">
                {this.labelName}
                <div className="input-field-group-container">
                    {this.inputGroup.map((value, i) => {
                        value.props.attr.index = i;
                        return value;
                        })
                    }
                    {this.addBtn}
                </div>
            </div>
        )
    }

}

class InputFieldPair extends React.Component {

    constructor(props) {
        super(props);
        if(props.inputGroup instanceof Array) {
            this.inputGroup = props.inputGroup;
        }
        this.removeSelf = this.removeSelf.bind(this);
        this.handleChange = this.handleChange.bind(this);
    }

    removeSelf() {
        this.props.removeSelf(this.props.attr.index); // remove this input field pair from list
    }

    handleChange(value) {
        this.props.attr.onChange(value, this.props.attr.index);
    }

    render() {
        return(
            <div className="row input-field-pair">
                <InputField onChange={this.handleChange} className="small" placeHolder="text" attr={{value: this.props.attr.value}}/>
                <RemoveElementButton className="input-field-remove-btn" onClick={this.removeSelf}/>
            </div>
        )
    }

}




