import React from 'react';
import './CurrentComponent.css'
import config from './Config'
import WelcomeComponent from './welcome/WelcomeComponent'
import ServiceComponent from './services/ServiceComponent'
import TermComponent from './terms/TermComponent'

export default class CurrentComponent extends React.Component {

    constructor() {
        super();
        this.setCurrentComponent = this.setCurrentComponent.bind(this);
        this.registeredComponents = this.components(this.setCurrentComponent);
        this.currentComp = this.registeredComponents.welcomeComponent;
    }

    components(cb) {
        return {
            welcomeComponent : <WelcomeComponent update={cb}/>,
            serviceComponent : <ServiceComponent update={cb}/>,
            termComponent : <TermComponent update={cb}/>
        };
    }

    setCurrentComponent(comp) {
        switch (comp) {
            case config.registeredComponents().welcomeComponent:
                this.currentComp = this.registeredComponents.welcomeComponent;
                break;
            case config.registeredComponents().serviceComponent:
                this.currentComp = this.registeredComponents.serviceComponent;
                break;
            case config.registeredComponents().termComponent:
                this.currentComp = this.registeredComponents.termComponent;
                break;
            default:
                this.currentComp = this.registeredComponents.welcomeComponent;
                break;
        }
        console.log('switch current component');
        this.forceUpdate();
    }

    render() {
        return(<div id="current_component">{this.currentComp}</div>);
    }

}



